import React, { useState } from "react"
import styled, { css, keyframes } from "styled-components"
import tw from "tailwind.macro"
import { Link } from "gatsby"
import StickyNav from "react-sticky-nav"
import LinkTo from "../components/LinkTo"
import { Container, Inner } from "../elements/Content"
import FLogo from "../images/logo-full.svg"

const stickyNavStyles = css`
  ${tw`bg-grey-lightest z-10 w-full`}
  /* TODO: Remove this reset when transition-link is fixed */
  position: fixed !important;
  box-shadow: none;
  transition: background-color 0.15s ease-out;

  &.pinned {
    ${tw`bg-white`};
    border-bottom: solid 2px #f5f6f4;
  }
`
const StickyNavShadow = styled.div`
  height: 72px;
  width: 100%;
`

const Nav = styled(Container)`
  ${tw`py-4`};
  transition: height 0.17s ease-out;

  @media only screen and (max-width: 900px) {
    height: ${props => (props.open ? "72px" : "250px")};
  }
`
const NavInner = styled(Inner)`
  ${tw`flex items-center justify-between flex-wrap`}
`

const LogoWrapper = styled.a`
  ${tw`flex text-black hover:text-black mr-4`};
  height: 40px;
`

const Logo = styled(FLogo)`
  height: 100%;
`

const HamburgerIcon = styled.div`
  ${tw`relative`};
`

const lineStyles = css`
  ${tw`bg-grey`};
  height: 2px;
  width: 26px;
  transform-origin: left top;
  transition: transform 0.2s ease, opacity 0.2s;
`
const LineTop = styled.div`
  ${lineStyles};
  transform: ${props =>
    props.open ? "none" : "translateX(2px) rotate(45deg)"};
`

const LineMiddle = styled.div`
  ${lineStyles};
  margin-top: 5px;
  margin-bottom: 5px;
  opacity: ${props => (props.open ? "1" : "0")};
  transform: ${props => (props.open ? "none" : "translateX(-50%)")};
`

const LineBottom = styled.div`
  ${lineStyles};
  transform: ${props =>
    props.open ? "none" : "translateY(4px) rotate(-45deg)"};
`

const TriggerWrapper = styled.div`
  ${tw`block lg:hidden`}
`

const Trigger = styled.div`
  ${tw`flex items-center p-3 cursor-pointer`};

  &:hover {
    ${LineTop},
    ${LineMiddle},
    ${LineBottom} {
      ${tw`bg-grey-darkest`};
    }
  }
`

const hiddenSmallOnly = css`
  ${tw`hidden lg:flex`};
`
const ListWrapper = styled.div`
  ${tw`w-full block flex-grow lg:flex lg:items-center lg:w-auto`};
  ${props => props.open && hiddenSmallOnly};
`

const animateItemsIn = keyframes`
  from {
    opacity: 0;
    transform: translateX(-70px);
  }

  to {
    opacity: 1;
    transform: translateX(0);
  }
`

const List = styled.div`
  ${tw`text-base lg:flex-grow pt-1`}
`

const ListItemStyles = css`
  ${tw`font-sans text-grey-dark hover:text-grey-darkest`};
  ${tw`text-xl lg:text-base cursor-pointer`};
  ${tw`mt-4 lg:mt-0`};

  animation-name: ${animateItemsIn};
  animation-duration: 0.3s;
  animation-fill-mode: both;
`

const ListItem = styled.span`
  ${ListItemStyles};
  ${tw`block lg:inline-block lg:mr-4`};
  ${tw`px-4 lg:px-0`};
  animation-delay: ${props => (props.index ? props.index * 0.1 : "0")}s;
`
const ListItemCTA = styled(Link)`
  ${ListItemStyles};
  ${tw`inline-block px-4 py-2 border rounded border-white hover:border-transparent hover:bg-white`}
  ${tw`font-bold leading-none`};
  animation-delay: ${props => (props.index ? props.index * 0.1 : "0")}s;
`

const Navigation = () => {
  const [open, setOpen] = useState(true)

  return (
    <>
      <StickyNavShadow />
      <StickyNav css={stickyNavStyles}>
        <Nav open={open}>
          <NavInner>
            <LogoWrapper href="/">
              <Logo />
            </LogoWrapper>
            <TriggerWrapper>
              <Trigger onClick={() => setOpen(!open)}>
                <HamburgerIcon>
                  <LineTop open={open} />
                  <LineMiddle open={open} />
                  <LineBottom open={open} />
                </HamburgerIcon>
              </Trigger>
            </TriggerWrapper>
            <ListWrapper open={open}>
              <List>
                <LinkTo href="/team">
                  <ListItem index={1}>Meet the Team</ListItem>
                </LinkTo>
              </List>
              <ListItemCTA
                index={2}
                to="/#contact"
                onClick={() => {
                  setOpen(true)
                }}
              >
                Contact
              </ListItemCTA>
            </ListWrapper>
          </NavInner>
        </Nav>
      </StickyNav>
    </>
  )
}

export default Navigation
export { LinkTo }
